import * as React from "react"
import { DataGrid } from "@mui/x-data-grid"
import {
  Button,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
  TextField,
} from "@mui/material"

const BangumiList = ({ columns, data, type }: any) => {
  const zhSimp =
    ""
  const zhTrad =
    ""

  const traditionalized = (cc: string) => {
    var str: string = ""
    for (var i = 0; i < cc.length; i++) {
      if (zhSimp.indexOf(cc.charAt(i)) != -1)
        str += zhTrad.charAt(zhSimp.indexOf(cc.charAt(i)))
      else str += cc.charAt(i)
    }
    return str
  }

  const [state, setState] = React.useState({
    filteredData: data,
    filterSearch: "",
    filterType: 0,
  })

  const onSearch = () => {
    setState({
      ...state,
      filteredData: data.filter((i: any) => {
        const valueSearch = state.filterSearch

        const title = traditionalized(i.title.toLowerCase())

        const season_id = i.season_id ? i.season_id.toString() : ""
        const media_id = i.media_id ? i.media_id.toString() : ""

        const acg_sn = i.acg_sn ? i.acg_sn.toString() : ""
        const anime_sn = i.anime_sn ? i.anime_sn.toString() : ""

        const splits = valueSearch.split(" ")

        let found = true

        splits.forEach(split => {
          if (
            !(
              (title.includes(traditionalized(split)) ||
                title.includes(split) ||
                (season_id != "" && season_id.includes(split)) ||
                (media_id != "" && media_id.includes(split)) ||
                (acg_sn != "" && acg_sn.includes(split)) ||
                (anime_sn != "" && anime_sn.includes(split)) ||
                (i.is_exclusive && split.includes("")) ||
                split.includes("") ||
                (i.is_vip && split.includes("")) ||
                split.includes("") ||
                (i.is_bilingual && split.includes("")) ||
                split.includes("")) &&
              (state.filterType == 0 ||
                (i.type && state.filterType != 0 && i.type == state.filterType))
            )
          ) {
            found = false
          }
        })
        return found
      }),
    })
  }

  const onKeyPress = (event: any) => {
    if (event.keyCode == 13) {
      onSearch()
    }
  }

  const onChangeFilterType = (event: any) => {
    setState({
      ...state,
      filterType: event.target.value,
    })
  }

  const onChangeSearch = (event: any) => {
    setState({
      ...state,
      filterSearch: event.target.value.toLowerCase(),
    })
  }

  if (type == "anigamer") {
    return (
      <div>
        <TextField
          style={{ margin: `4px` }}
          size="small"
          label=""
          variant="outlined"
          placeholder="||"
          onChange={onChangeSearch}
          onKeyDown={onKeyPress}
        />
        <Button
          variant="contained"
          color="primary"
          onClick={onSearch}
          style={{ margin: `4px` }}
        >
          
        </Button>
        <div style={{ overflowX: "scroll", tableLayout: "fixed" }}>
          <DataGrid
            rows={state.filteredData}
            getRowId={row => row.anime_sn}
            columns={columns}
            pageSize={50}
            autoHeight
            sortModel={[{ field: "anime_sn", sort: "desc" }]}
          />
        </div>
      </div>
    )
  } else if (type == "bilibili_sea") {
    return (
      <div>
        <TextField
          style={{ margin: `4px` }}
          size="small"
          label=""
          variant="outlined"
          placeholder="||"
          onChange={onChangeSearch}
          onKeyDown={onKeyPress}
        />
        <Button
          variant="contained"
          color="primary"
          onClick={onSearch}
          style={{ margin: `4px` }}
        >
          
        </Button>
        <div style={{ overflowX: "scroll", tableLayout: "fixed" }}>
          <DataGrid
            rows={state.filteredData}
            getRowId={row => row.season_id}
            columns={columns}
            pageSize={50}
            autoHeight
            sortModel={[{ field: "season_id", sort: "desc" }]}
          />
        </div>
      </div>
    )
  } else {
    return (
      <div>
        <TextField
          style={{ margin: `4px` }}
          size="small"
          label=""
          variant="outlined"
          placeholder="||"
          onChange={onChangeSearch}
          onKeyDown={onKeyPress}
        />
        <Button
          variant="contained"
          color="primary"
          onClick={onSearch}
          style={{ margin: `4px` }}
        >
          
        </Button>
        <br />
        <FormControl component="fieldset">
          <FormLabel component="legend"></FormLabel>
          <RadioGroup
            row
            aria-label="filter"
            name="filter"
            defaultValue="0"
            onChange={onChangeFilterType}
          >
            <FormControlLabel value="0" control={<Radio />} label="" />
            <FormControlLabel value="1" control={<Radio />} label="" />
            <FormControlLabel value="2" control={<Radio />} label="" />
            <FormControlLabel value="3" control={<Radio />} label="" />
            <FormControlLabel value="4" control={<Radio />} label="" />
            <FormControlLabel value="5" control={<Radio />} label="" />
            <FormControlLabel value="7" control={<Radio />} label="" />
          </RadioGroup>
        </FormControl>
        <div style={{ overflowX: "scroll", tableLayout: "fixed" }}>
          <DataGrid
            rows={state.filteredData}
            getRowId={row => row.season_id}
            columns={columns}
            pageSize={50}
            autoHeight
            sortModel={[{ field: "season_id", sort: "desc" }]}
          />
        </div>
      </div>
    )
  }
}

export default BangumiList
